@import url('https://fonts.googleapis.com/css2?family=Cousine:ital,wght@0,400;0,700;1,400;1,700&family=Faustina:wght@300;400;500&family=Lora&family=Playfair+Display&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    font-family: sans-serif;
}

ul li {
    list-style: none;
}

body {
    background-color: dimgrey;
    background-color: rgb(80, 80, 80);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 100%);
    background: rgb(0, 0, 0);
    background-color: rgb(0, 0, 0);
    background-color: #d2d2d2;
    overflow-x: hidden;
}

#cursor {
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: yellow;
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100;
}


/* RZEŹBA - PARALAXA */

.sculpture {
    background-image: url('/public/photo2.png');
    height: 100vh;
    /*margin-right: 15%;*/
    width: auto;
    background-position: top;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    overflow: hidden;
}

.parallax-container,
.parallax-image {
    overflow: hidden;
}


/*  NAWIGACJA  */

.sections__nav {
    transform: rotate(270deg);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-right: auto;
    z-index: 99;
    margin-right: auto;
    width: fit-content;
    width: 4.75%;
    width: 75px;
    display: none;
    position: absolute;
    top: 30%;
}

.sections__nav.nav__open {
    display: flex;
}

.sections__nav li {
    font-family: 'Cousine', monospace;
    font-size: 1.25rem;
    margin: 0 0.75rem;
    cursor: pointer;
    transition: .2s;
}

.sections__nav li:hover {
    color: aqua;
    /* PASEK PRZEZ ŚRODEK */
}


/*  HAMBURGER MENU  */

.navigation {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 2.25rem;
    margin-top: 2rem;
}

label {
    display: flex;
    flex-direction: column;
    width: 70px;
    cursor: pointer;
}

label span {
    background: #000;
    border-radius: 10px;
    height: 7px;
    margin: 7px 0;
    transition: .4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
}

span:nth-of-type(1) {
    width: 50%;
}

span:nth-of-type(2) {
    width: 100%;
}

span:nth-of-type(3) {
    width: 75%;
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]:checked~span:nth-of-type(1) {
    transform-origin: bottom;
    transform: rotatez(45deg) translate(8px, 0px)
}

input[type="checkbox"]:checked~span:nth-of-type(2) {
    transform-origin: top;
    transform: rotatez(-45deg)
}

input[type="checkbox"]:checked~span:nth-of-type(3) {
    transform-origin: bottom;
    width: 50%;
    transform: translate(30px, -11px) rotatez(45deg);
}

.app,
.todos {
    margin-top: 10vh;
}

.tasks {
    list-style: none;
}

.task {
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    font-family: 'Roboto', sans-serif;
    font-family: 'Cousine', monospace;
    font-size: 8rem;
    font-weight: bold;
    font-style: italic;
    color: black;
    position: absolute;
    top: 20%;
    right: 25%;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
}

.name span {
    font-family: 'Cousine', monospace;
    margin-left: 30%;
    opacity: 0;
    animation: fadeIn 0.5s ease-in-out forwards;
}


/* ANIMACJA POJAWIANIA SIĘ IMIENIA & NAZWISKA */

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.name:nth-child(1) {
    animation-delay: 0.15s;
}

.name:nth-child(2) {
    animation-delay: 0.3s;
}

.name:nth-child(3) {
    animation-delay: 0.45s;
}

.name:nth-child(4) {
    animation-delay: 0.6s;
}

.name:nth-child(5) {
    animation-delay: 0.75s;
}

.name:nth-child(6) {
    animation-delay: 0.9s;
}

.name span:nth-child(1) {
    animation-delay: 0.15s;
}

.name span:nth-child(2) {
    animation-delay: 0.3s;
}

.name span:nth-child(3) {
    animation-delay: 0.45s;
}

.name span:nth-child(4) {
    animation-delay: 0.6s;
}

.name span:nth-child(5) {
    animation-delay: 0.75s;
}

.name span:nth-child(6) {
    animation-delay: 0.9s;
}